li {
  all: unset;
}

.card {
  min-height: 220px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 220px;
  padding: 20px;
  border: 1px solid rgba(0, 0, 0, 0.25);
}
.name {
  text-align: center;
  font-size: 15px;
}
img {
  width: 110px;
  height: 100px;
  margin-bottom: 10px;
}
.info {
  display: flex;
  flex-direction: column;
}
button {
  width: 50%;
  border: none;
  font-size: 20px;
  border-radius: 5px;
  background-color: silver;
  cursor: pointer;
}

@media screen and (max-width: 400px) {
  .card {
    min-height: 150px;
    width: 100px;
  }
  img {
    width: 75px;
    height: 50px;
    margin-bottom: 5px;
  }
  .card span {
    font-size: 13px;
  }
}
